.music {
    display: -ms-flexbox;
    display: flex;
    position: relative;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding-top: 2em;
    margin: 0.5em;
}

.music__bar {    
    display: -ms-flexbox;    
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 0.5em;
    height: 2.5em;
    margin: 0.5em 0;
}

.music__bar {
    border-left: solid black 0.05em;
}

.music__bar:last-child {
    border-right: double black 0.25em;
}

.music__tempo {
    position: absolute;
    top: 0;
    left: 0;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-align: baseline;
        align-items: baseline;
}

.music__tempo .note {
    font-size: 0.75em;
    background-size: 75%;
}

.music__tempo[data-beat="4"]::before {
    content: 'crotchet = ';
}

.music__tempo[data-beat="2"]::before {
    content: 'minim = ';
}

.music__tempo[data-beat="8"]::before {
    content: 'quaver = ';
}

.music__bar--timeSignature + .music__bar {
    border-left: none !important;
}

.timeSignature {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    font-weight: bolder;
    padding: 0 0.5em;
}


